import Vue from 'vue'
import Router from 'vue-router'
import Home from './components/Home'
import Delivery from './components/about/Delivery'
import OrderDocument from './components/about/OrderDocument'
import History from './components/about/History'

Vue.use(Router)

export default new Router({
    mode: 'history',
    //路由滚动
    // scrollBehavior (){
    //     return {x: 0, y: 100}
    // },
    // 返回 savedPosition，在按下 后退/前进 按钮时，就会像浏览器的原生表现那样：
    // scrollBehavior (savedPosition) {
    //     if (savedPosition) {
    //       return savedPosition
    //     } else {
    //       return { x: 0, y: 0 }
    //     }
    //   },
    routes: [
        {
            path: '/',
            name: 'Home',
            // 实现router-view的复用
            components: {
                default: Home,
                'delivery':Delivery,
                'orderdocument': OrderDocument,
                'history': History 
            }
        },
        {
            path: '/about',
            name: 'About',
            component: () => import('./components/about/About.vue'),
            redirect: '/contact',
            children:[
                {
                    path: '/history',
                    name: 'historyLink',
                    component: ()=> import('./components/about/History.vue')
                },
                {
                    path: '/delivery',
                    name: 'deliveryLink',
                    component: ()=> import('./components/about/Delivery.vue')
                },
                {
                    path: '/contact',
                    name: 'contactLink',
                    component: ()=> import('./components/about/Contact.vue'),
                    redirect: '/contact/contactperson',
                    children:[
                        {
                            path: '/contact/contactperson',
                            name: 'contactpersonLink',
                            component: ()=> import('./components/about/ContactPerson.vue')
                        },
                        {
                            path: '/contact/phone',
                            name: 'phoneLink',
                            component: ()=> import('./components/about/Phone.vue')
                        }
                    ]
                },
                {
                    path: '/orderdocument',
                    name: 'orderdocumentLink',
                    component: ()=> import('./components/about/OrderDocument.vue')
                },
            ] 
        },
        {
            path: '/menu',
            name: 'Menu',
            component: () => import('./components/Menu.vue')
        },
        {
            path: '/login',
            name: 'Login',
            component: () => import('./components/Login.vue')
        },
        {
            path: '/register',
            name: 'Reg',
            component: () => import('./components/Register.vue')
        },
        {
            path: '/admin',
            name: 'Admin',
            component: () => import('./components/Admin.vue'),
            // 路由独享守卫
            // beforeEnter: (to, from, next) => {
            //     // ...
            //     if( to.path == '/login' || to.path == '/register'){
            //         next();
            //     }else{
            //         alert('未登录状态,请先登录');
            //         next('/login');
            //     }
            // }
        },
        {
            path: '*',
            redirect: '/'
        }
    ]
})